import { useEffect, useState } from "react";
import { useFinanceReceipt } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { admissionModifyReceiptData } from "../../../../pdf/function/pdf-convertor-function";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../UserStudentMember/AdmissionNotification/AdmissionNotification.module.css";
import { pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
const StudentAdmissionFeesStatusReceipt = ({ rid, receiptFile }) => {
  const [receiptId, setReceiptId] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [downloadableData, setDownloadableData] = useState("");
  const { financeReceipt, financeReceiptRefetch } = useFinanceReceipt({
    rid: receiptId,
    skip: !receiptId,
  });
  useEffect(() => {
    if (receiptId) financeReceiptRefetch();
  }, [receiptId, financeReceiptRefetch]);
  useEffect(() => {
    if (financeReceipt?.receipt && financeReceipt?.all_remain) {
      setDownloadableData(financeReceipt);
    }
  }, [financeReceipt]);

  useEffect(() => {
    if (downloadableData) {
      const workerInstance = new Worker(
        new URL(
          "../../../../pdf/worker/main.receipt.pdf.worker.js",
          import.meta.url
        )
      );

      const { institute, studentInfo, paymentReceiptInfo } =
        admissionModifyReceiptData(
          downloadableData?.all_remain,
          downloadableData?.receipt
        );
      workerInstance.postMessage({
        institute,
        studentInfo,
        paymentReceiptInfo,
        printReceiptType:
          paymentReceiptInfo?.transactonRefund === "Refunded"
            ? "ADMISSION_REFUND"
            : "ADMISSION",
      });

      workerInstance.onmessage = (message) => {
        if (message) {
          window.open(message.data, "_blank");
          setDisabled((pre) => !pre);
          setDownloadableData("");
          workerInstance.terminate();
        }
      };
    }
  }, [downloadableData]);
  const onReceiptDownload = () => {
    setDisabled((pre) => !pre);
    setReceiptId(rid);
    if (receiptId === rid) {
      setDownloadableData(financeReceipt);
    }
  };
  return (
    <>
      {receiptFile ? (
        <a
          href={`${pdfCdnUrl}/${receiptFile}`}
          target="_blank"
          rel="noopener noreferrer"
        >
          <div
            className={style.ad_button}
            style={{
              backgroundColor: "#def3e1",
              borderColor: "def3e1",
            }}
          >
            <span
              style={{
                color: "#008000",
              }}
            >
              Click here to view fee receipt
              <img
                src={`${assestsUserFinanceUrl}/receipt.svg`}
                alt="receipt icon"
                style={{
                  cursor: "pointer",
                  height: "1rem",
                  width: "auto",
                  marginLeft: "0.6rem",
                }}
              />
            </span>
          </div>
        </a>
      ) : (
        <div
          className={style.ad_button}
          style={{
            backgroundColor: "#def3e1",
            borderColor: "def3e1",
          }}
        >
          <span
            style={{
              color: "#008000",
            }}
            // onClick={onReceiptDownload}
          >
            Click here to view fee receipt
            <img
              src={`${assestsUserFinanceUrl}/receipt.svg`}
              alt="receipt icon"
              style={{
                cursor: "pointer",
                height: "1rem",
                width: "auto",
                marginLeft: "0.6rem",
              }}
            />
          </span>
        </div>
      )}

      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentAdmissionFeesStatusReceipt;
